










import Vue from "vue";
import { mapGetters, mapActions } from "vuex";
import Row from "./Row.vue";

export default Vue.extend({
  name: "Game",
  components: {
    Row
  },
  computed: {
    ...mapGetters(["getRow"])
  },
  mounted: function() {
    this.distributeCard();
  },
  methods: {
    ...mapActions(["distributeCard"])
  }
});
